import React from "react";
import { ColorText } from "styles/globals";
import { Label, MsgError, Input } from "jz-validation-form";
import styled from "styled-components";

const ContainerEye = styled.div`
  & svg {
    position: relative;
    bottom: 50px;
    left: 370px;
    margin-bottom: -32px;
  }
  position: absolute;
`;

interface V {
  label: string;
  name: string;
  value: any;
  errors: any;
  type: string;
}

interface Params {
  v: V;
  handleChange: (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => void;
  handleShowPass: () => void;
  EyeJSX: () => JSX.Element;
}

const ContentFormLogin = (props: Params) => {
  function showInputPassword() {
    return (
      <>
        <Input
          type={props.v.type}
          name={props.v.name}
          onChange={props.handleChange}
          value={props.v.value}
          required
        />
        <ContainerEye onClick={props.handleShowPass}>
          <props.EyeJSX />
        </ContainerEye>
      </>
    );
  }

  return (
    <>
      <Label colorText={ColorText}>{props.v.label}</Label>
      {props.v.name === "password" ? (
        showInputPassword()
      ) : (
        <Input
          type={props.v.type}
          name={props.v.name}
          onChange={props.handleChange}
          value={props.v.value}
          required
        />
      )}

      {props.v.errors && <MsgError>{props.v.errors}</MsgError>}
    </>
  );
};

export default ContentFormLogin;
